<script>
import { socket } from "@/socket";

export default {
  name: "ConfigOrdemProducao",
  data() {
    return {
      OrdemProd: ''
    }
  },
  methods: {
    listenOrdemProducao() {
      socket.on('ordemProducao', (arg) => {
        this.OrdemProd = arg
        this.$store.commit('addOrdemProducao', this.OrdemProd)
      })
    },
  },
  mounted() {
    this.listenOrdemProducao()
  }

};
</script>

