                </base-card>
                <base-card>
                    <h2>ID</h2>
                    <h2>{{ parameters.id }}</h2>
                </base-card>
                <base-card>
                    <h2>CONSUMÍVEL</h2>
                    <h2>{{ parameters.consumivel }}</h2>
                </base-card>
            </div>
            <div class="col-9">

                <div class="row">

                    <base-card>

                        <div class="row">
                            <img src="@/assets/images/MENSAGEM.svg" alt="">
                        </div>
                        <h1>MENSAGEM</h1>
                        <h4>DESEJA RETOMAR O PROCESSO ANTERIOR?</h4>
                        <!-- <h2>DADOS:</h2> -->
                    </base-card>

                </div>



                <div class="row">
                    <div class="col">
                        <base-card class="retomar">
                            <h1>RETOMAR</h1>
                            <h2>TECLE ENTER</h2>
                        </base-card>
                    </div>
                    <div class="col">
