<template>
    <the-toolbar></the-toolbar>
    <router-view v-slot="{ Component }">
        <transition name="slide-fade">
            <component :is="Component"></component>
        </transition>
    </router-view>
    <img class="fundoIcon" src="@/assets/images/fundo.png" />
</template>

<script>
import Swal from 'sweetalert2'
import { socket } from './socket'
import TheToolbar from './components/layout/TheToolbar.vue';

export default {
    name: 'App',
    methods: {
        displayAlert() {
            socket.on('swal', (data) => {
                Swal.fire({
                    title: data.title,
                    text: data.text,
                    icon: data.type || "error",
                    timer: data.time || 2500,
                    showConfirmButton: false,
                });
            });
        },
        changePage() {
            socket.on('changepath', (pageName) => {
                this.$router.push({ name: pageName, params: {} });
            });
        },
        listenKeyboard() {
            window.addEventListener("keypress", e => {
                const key = String.fromCharCode(e.keyCode);
                socket.emit('keyboard-listener', key)
            });
        }
    },
    mounted() {
        this.displayAlert();
        this.changePage();
        this.listenKeyboard();
    },
    components: { TheToolbar }
}
</script>

<style>
.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.15s;
}

.fade-enter,
.fade-leave-to {
    opacity: 0;
}

.slide-fade-enter-active {
    transition: all 0.4s ease-out;
}

.slide-fade-leave-active {
    transition: all 0.01s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
    transform: translateX(20px);
    opacity: 0;
}

.fundoIcon {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    z-index: -1;
    /* object-fit: cover; */
}

h1 {
    text-align: center;
    font-size: var(--font-size-xl);
    /* color: var(--color-white); */
    font-family: "Roboto";
}

h2 {
    text-align: center;
    font-size: var(--font-size-xl);
    /* color: var(--color-white); */
    font-family: "Roboto";
}

h3 {
    font-family: "Roboto";
}

h4 {
    font-family: "Roboto";
}

h5 {
    font-family: "Roboto";
}

p {
    font-family: "Roboto";
}
</style>